.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  height: 60px;
  background-color: #2d2d2d;
  border-bottom: 2px solid #666666;
}

.header-left {
  display: flex;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 1.2rem;
  color: #ffffff;
}

.header-nav {
  display: flex;
  gap: 1rem;
}

.nav-button {
  padding: 0.5rem 1rem;
  border: 1px solid #555555;
  background-color: #3a3a3a;
  color: #cccccc;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.nav-button:hover {
  background-color: #4a4a4a;
  color: #ffffff;
}

.header-right {
  display: flex;
  align-items: center;
}

.profile-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  background-color: #404040;
  border: 1px dashed #666666;
}

/* Responsive design */
@media (max-width: 768px) {
  .header {
    padding: 1rem;
    flex-direction: column;
    height: auto;
    gap: 1rem;
  }
  
  .header-nav {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .nav-button {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
} 