.right-sidebar {
  width: 320px;
  padding: 1.5rem;
  background-color: #2d2d2d;
  border-left: 2px solid #666666;
  overflow-y: auto;
}

.right-sidebar section {
  margin-bottom: 2rem;
}

.right-sidebar h3 {
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

/* News Feed */
.news-feed {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.news-item {
  padding: 1rem;
  border-radius: 4px;
}

.news-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.news-time {
  font-size: 0.8rem;
  color: #999999;
}

.news-source {
  font-size: 0.8rem;
  font-weight: 500;
  color: #cccccc;
}

.news-title {
  font-size: 0.9rem;
  font-weight: 500;
  color: #ffffff;
  margin-bottom: 0.5rem;
  line-height: 1.4;
}

.news-sentiment {
  display: inline-block;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: bold;
}

.news-sentiment.positive {
  background-color: #1a4d1a;
  color: #66ff66;
}

.news-sentiment.negative {
  background-color: #4d1a1a;
  color: #ff6666;
}

.news-sentiment.neutral {
  background-color: #404040;
  color: #cccccc;
}

/* AI Summary Section */
.ai-summary-section {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.ai-summary, .ai-insight, .ai-prediction {
  padding: 1rem;
  border-radius: 4px;
}

.summary-header, .insight-header, .prediction-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.ai-icon {
  font-size: 1.2rem;
}

.summary-content h4 {
  font-size: 0.9rem;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 0.5rem;
}

.summary-content ul {
  list-style: none;
  padding: 0;
}

.summary-content li {
  font-size: 0.8rem;
  color: #cccccc;
  margin-bottom: 0.25rem;
  padding-left: 1rem;
  position: relative;
}

.summary-content li:before {
  content: "•";
  position: absolute;
  left: 0;
  color: #ffffff;
}

.ai-insight p, .ai-prediction p {
  font-size: 0.8rem;
  color: #cccccc;
  line-height: 1.4;
}

/* Quick Stats */
.stats-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.stat-item {
  padding: 1rem;
  text-align: center;
  border-radius: 4px;
}

.stat-number {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 0.25rem;
}

.stat-label {
  font-size: 0.8rem;
  color: #cccccc;
}

/* Responsive design */
@media (max-width: 768px) {
  .right-sidebar {
    width: 100%;
    border-left: none;
    border-top: 2px solid #666666;
    padding: 1rem;
  }
  
  .stats-grid {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 480px) {
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .news-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
} 