.footer {
  padding: 2rem;
  background-color: #2d2d2d;
  border-top: 2px solid #666666;
}

.footer-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

.footer-section h4 {
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
  margin-bottom: 1rem;
}

.footer-section p {
  font-size: 0.9rem;
  color: #cccccc;
  line-height: 1.4;
}

.footer-links {
  list-style: none;
  padding: 0;
}

.footer-links li {
  margin-bottom: 0.5rem;
}

.footer-link {
  font-size: 0.9rem;
  color: #cccccc;
  text-decoration: none;
  transition: color 0.2s;
}

.footer-link:hover {
  color: #ffffff;
}

.footer-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid #555555;
  font-size: 0.8rem;
  color: #cccccc;
}

.footer-stats {
  display: flex;
  gap: 1rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .footer {
    padding: 1rem;
  }
  
  .footer-content {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 1rem;
  }
  
  .footer-bottom {
    flex-direction: column;
    gap: 1rem;
    text-align: center;
  }
  
  .footer-stats {
    flex-direction: column;
    gap: 0.5rem;
  }
} 