.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.dashboard-container {
  display: flex;
  flex: 1;
  min-height: calc(100vh - 120px); /* Account for header and footer */
}

/* Responsive design */
@media (max-width: 768px) {
  .dashboard-container {
    flex-direction: column;
  }
} 