.main-dashboard {
  flex: 1;
  padding: 1.5rem;
  background-color: #1a1a1a;
  overflow-y: auto;
}

/* Cards Section */
.cards-section {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.card-group h3 {
  margin-bottom: 1rem;
  font-size: 1.2rem;
  font-weight: bold;
  color: #ffffff;
}

.cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1rem;
}

/* Topic Cards */
.topic-card {
  padding: 1rem;
  min-height: 150px;
  display: flex;
  flex-direction: column;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.card-header h4 {
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.sentiment-meter {
  width: 60px;
  height: 8px;
  background-color: #404040;
  border-radius: 4px;
  overflow: hidden;
}

.meter-fill {
  height: 100%;
  background-color: #ffffff;
  transition: width 0.3s ease;
}

.card-content p {
  font-size: 0.9rem;
  color: #cccccc;
  margin-bottom: 1rem;
  flex: 1;
}

.source-icons {
  display: flex;
  gap: 0.5rem;
}

/* Alert Cards */
.alert-card {
  padding: 1rem;
  min-height: 120px;
}

.alert-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.alert-header h4 {
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.credibility-score {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: bold;
}

.credibility-score.low {
  background-color: #4d1a1a;
  color: #ff6666;
}

.credibility-score.medium {
  background-color: #4d3a1a;
  color: #ffcc66;
}

.alert-content p {
  font-size: 0.9rem;
  color: #cccccc;
  margin-bottom: 0.5rem;
}

.verified-badge {
  font-size: 0.8rem;
  font-weight: bold;
  color: #ff6666;
}

/* Climate Cards */
.climate-card {
  padding: 1rem;
  min-height: 120px;
}

.climate-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.climate-header h4 {
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.aqi-meter, .fire-meter, .rain-meter {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: bold;
}

.aqi-meter.high {
  background-color: #4d1a1a;
  color: #ff6666;
}

.fire-meter.critical {
  background-color: #4d0000;
  color: #ff9999;
}

.rain-meter.moderate {
  background-color: #1a4d4d;
  color: #66ccff;
}

.climate-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.climate-content p {
  font-size: 0.9rem;
  color: #cccccc;
  flex: 1;
}

.climate-icon {
  font-size: 1.5rem;
  margin-left: 1rem;
}

/* Responsive design */
@media (max-width: 768px) {
  .main-dashboard {
    padding: 1rem;
  }
  
  .cards-row {
    grid-template-columns: 1fr;
  }
} 