.sidebar {
  width: 280px;
  padding: 1.5rem;
  background-color: #2d2d2d;
  border-right: 2px solid #666666;
  overflow-y: auto;
}

.sidebar-section {
  margin-bottom: 2rem;
}

.sidebar-section h3 {
  margin-bottom: 1rem;
  font-size: 1rem;
  font-weight: bold;
  color: #ffffff;
}

.filter-group {
  margin-bottom: 1rem;
}

.filter-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  font-weight: 500;
  color: #cccccc;
}

.filter-select {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #555555;
  background-color: #3a3a3a;
  font-size: 0.9rem;
  color: #ffffff;
}

.toggle-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.toggle-btn {
  padding: 0.75rem 1rem;
  border: 1px solid #555555;
  background-color: #3a3a3a;
  color: #cccccc;
  cursor: pointer;
  font-size: 0.9rem;
  text-align: left;
  transition: all 0.2s;
}

.toggle-btn:hover {
  background-color: #4a4a4a;
  color: #ffffff;
}

.toggle-btn.active {
  background-color: #1a1a1a;
  color: #ffffff;
  border-color: #666666;
}

.quick-actions {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.action-btn {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  border: 1px solid #555555;
  background-color: #3a3a3a;
  color: #cccccc;
  cursor: pointer;
  font-size: 0.9rem;
  text-align: left;
  transition: all 0.2s;
}

.action-btn:hover {
  background-color: #4a4a4a;
  color: #ffffff;
}

/* Responsive design */
@media (max-width: 768px) {
  .sidebar {
    width: 100%;
    border-right: none;
    border-bottom: 2px solid #666666;
    padding: 1rem;
  }
  
  .toggle-buttons {
    flex-direction: row;
    flex-wrap: wrap;
  }
  
  .toggle-btn {
    flex: 1;
    min-width: 120px;
  }
} 